<template>
  <div class="ratingSelect">
   <div class="ratingsType">
   	<span class="all" :class="{active:selectType==2}" @click="select(2)" >{{desc.all}}<span  class="count" >{{ratings.length}}</span></span>
   	<span class="positive" :class="{active:selectType==0}" @click="select(0)">{{desc.positive}}<span class="count" >{{POSITIVE.length}}</span></span>
   	<span class="negative" :class="{active:selectType==1}" @click="select(1)">{{desc.negative}}<span class="count" >{{NEGATIVE.length}}</span></span>
   </div>
    <div class="switch" :class="{on:onlycontent}">
    	<i class="icon-check_circle" @click="toggle"></i>
    	<span class="text">只看有内容的评价</span>
    </div>
  </div>
</template>

<script>
const POSITIVE=0
const NEGATIVE=1
const ALL=2
export default {
  props:{
	  ratings:{
	  	type:Array,
	  	default(){
	  		return []
	  	}
	  },
  	selectType:{
  		
  		type:Number,
  		default:ALL
  	},
  	desc:{
  		type:Object,
  		default(){
  		return{

  			positive:"满意",
  			negative:"不满意",
  			all:"全部"
  		}
  		}	
  		},
  		onlycontent:{
  			type:Boolean,
  			default:false
  		}
  	
  },
  data(){
  	return {}
  },
  methods:{
  	select(type){
  		this.$emit("select",type)
  	},
  	toggle(){
  		this.$emit("toggle",!this.onlycontent)
  	}
  },
  computed:{
  	POSITIVE(){
  		return this.ratings.filter((rating)=>{
  			return rating.rateType==POSITIVE
  		})
  	},
  		NEGATIVE(){
  		return this.ratings.filter((rating)=>{
  			return rating.rateType==NEGATIVE
  		})
  	}

  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.ratingSelect{
		border-bottom: 1px solid rgba(7,17,27,0.1);
	}
	.ratingSelect .ratingsType{
		padding: 18px 0;
		margin:0 18px;
		border-bottom: 1px solid rgba(7,17,27,0.1);
	
		font-size: 12px;
		line-height: 16px;
	}
	.ratingSelect .ratingsType .all .count,.ratingSelect .ratingsType .positive .count,.ratingSelect .ratingsType .negative .count{
		margin-left: 2px;
	}
	.ratingSelect .ratingsType .all{
		border-radius: 1px;
		padding: 8px 12px;
		margin-right: 8px;
		background: rgba(0,160,220,0.2);
		color:rgb(77,85,93);
	}
	.ratingSelect .ratingsType .all.active,.ratingSelect .ratingsType .positive.active{
		background: rgba(0,160,220,1);
		color:#fff;

	}
	.ratingSelect .ratingsType .positive{
		border-radius: 1px;
		padding: 8px 12px;
		margin-right: 8px;
		color: rgb(77,85,93);
		background: rgba(0,160,220,0.1);
	}
	.ratingSelect .ratingsType .negative{
		border-radius: 1px;
		padding: 8px 12px;
		color:rgb(77,85,93);
		background: rgba(77,85,93,0.1);
	}
	.ratingSelect .ratingsType .negative.active{
		background: rgba(77,85,93,1);
		color:#fff;
	}
	.ratingSelect .switch{
		padding: 12px 18px;
		color: rgb(147,153,159);

	}
	.ratingSelect  .switch.on .icon-check_circle{
		color: #00c850;
	}
	.ratingSelect  .switch .icon-check_circle{
		margin-top: 12px;
		vertical-align: middle;
		margin-right: 4px;
		font-size: 24px;
		line-height: 24px;
	}
	.ratingSelect  .switch .text{
		vertical-align: middle;
		font-size: 12px;
		line-height: 24px;
	}
</style>



