<template>
  <div class="seller" ref="seller">
   	<div class="seller-content">
   		<div class="overview">
   			<h1 class="title">{{seller.name}}</h1>
   			<div class="desc">
   			<div class="collection" @click="select">
   				<span class="icon-favorite" :class="{on:collection}"></span>
   				<div class="text">{{collectionText}}</div>

   			</div>
   			<div class="stratLine">
   			<span class="start-wrapper">
   				<start :size="36" :score='seller.score' ></start>
   			</span>
   			<span class="ratingCount">({{seller.ratingCount}})</span>
   			<span class="sellCount">月售{{seller.sellCount}}单</span>
   			</div>
        <div class="remark">	
   				<div class="minPrice">起送价<div class="normal">{{seller.minPrice}}<span>元</span> </div></div>
   				<div class="deliveryPrice">商家配送<div class="normal">{{seller.deliveryPrice}}<span>元</span></div></div>
   				<div class="deliveryTime">平均配送时间<div class="normal">{{seller.deliveryTime}}<span>分钟</span></div></div>
         </div> 
			</div>
			
   		</div>
   		<split></split>
   		<div class="notice">
   			<h1 class="title">公告与活动</h1>
   			<p class="bulletin">
   				{{seller.bulletin}}
   			</p>
        
   			<ul class="supports">
   				<li class="support" v-for="support in seller.supports">
   					<span class="icon" :class="classMap[support.type]" v-show="support.type>=0"></span>
   					<span class="description">{{support.description}}</span>
   				</li>
   			</ul>
        
   		</div>
   		<split></split>
   		<div class="live-action">
   		<div class="title">商家实景</div>
      <div class="pics-wrapper" ref="picWrapper">
   		<ul class="pics" ref="picture">
   			<li v-for="(pic,index) in seller.pics" class="pic">
   				<img :src="pic" alt="" width="120" height="90">
   			</li>
   		</ul>
    </div>
   		</div>
   		<split></split>
   		<div class="seller-infos">
   			<div class="title">商家信息</div>
   			<ul class="infos">
   				<li class="info" v-for="info in seller.infos">
   					{{info}}
   				</li>
   			</ul>
   		</div>
   	</div>
  </div>
</template>

<script>
import start  from '../start/start'
import split from '../split/split'
import BScroll from 'better-scroll'

const store={
  save(key,value){
    localStorage.setItem(key,JSON.stringify(value))
  },
  fetch(key){
    return JSON.parse(localStorage.getItem(key))
  }
}
let selected=store.fetch("selected")
let collectionText=store.fetch("collectionText")
export default {
  components:{
  	start,
  	split
  },
  props:{
  	seller:{
  		type:Object
  	}
  },
  watch:{
      collection:{
        handler(){
           store.save("selected",this.collection)
        store.save("collectionText",this.collectionText)
      },
      deep:true
       
      }
  },
  data(){
  	return{
  		classMap:[],
  		collection:selected,
  		collectionText:collectionText
  	}
  },
  methods:{

  	select(){
    //     if(this.collectionText==null){
    //   this.collectionText="未收藏"
    // }
  		this.collection=!this.collection
  		
  			if(this.collection){
  			this.collectionText="已收藏"
  		}else{
  			this.collectionText="未收藏"
  		}
  	}
  },
  mounted(){
      console.log(this.collection+","+this.collectionText)
     this.$nextTick(()=>{
     
      if(!this.sellerScroll){
        this.sellerScroll=new BScroll(this.$refs.seller,{
        click:true
      })
      }else{
        this.sellerScroll.refresh()
      }
      
     })
     if(this.seller.pics){
      let width=120
      let margin=6
      this.$refs.picture.style.width=((width+margin)*this.seller.pics.length-margin)+'px'
      this.$nextTick(()=>{
         if(!this.picWrapperScroll){
        this.picWrapperScroll=new BScroll(this.$refs.picWrapper,{
          click:true,
          scrollX:true,
          eventPassthrough:'vertical'
        })
      }else{
         this.picWrapperScroll.refresh()
      }
      })
     }

  },
  created(){
  	this.classMap=['decrease','discount','guarantee','invoice','special']
  	
  }





}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.seller{
	position: absolute;
    top: 174px;
    bottom: 0;
    left: 0;
    width: 100%;
    overflow: hidden;
}
.seller-content .overview{
	padding: 18px;
}
.seller-content .overview .title{
	margin-bottom: 8px;
	font-weight: 700;
	font-size: 14px;
	color: rgb(7,17,27);
	line-height: 14px;
}
.seller-content .overview  .desc{
	position: relative;
}
.seller-content .overview  .desc .collection{
	position: absolute;
	text-align: center;
	top:-18px;
	right: 0px;

}
.seller-content .overview  .desc .collection .icon-favorite{
	font-size: 24px;
	line-height: 24px;
  color: #d4d6d9;
}
.seller-content .overview  .desc .collection .icon-favorite.on{
	color: rgb(240,20,20);
}
.seller-content .overview  .desc .collection .text{
	margin-top: 4px;
	font-size: 10px;
	color: rgb(77,85,93);
	line-height: 10px;
}
.seller-content .overview  .desc .stratLine{
	padding-bottom: 18px;
	border-bottom: 1px solid rgba(7,17,27,0.1);
	font-size: 0px;
	color: rgb(77,85,93);
	line-height: 18px;
}
.seller-content .overview .desc .start-wrapper{
	margin-right: 8px;
	font-size: 10px;
}
.seller-content .overview .desc .ratingCount{
	margin-right: 12px;
	font-size: 10px;
}
.seller-content .overview .desc .sellCount{
	font-size: 10px;
}
.seller-content .overview .desc .remark{
  display: flex;

}
.seller-content .overview .desc .minPrice, .seller-content .overview .desc .deliveryPrice, .seller-content .overview .desc .deliveryTime{
	border-right: 1px solid rgba(7,17,27,0.1);
   text-align: center;
	padding: 18px 18px 0 18px;
	flex: 1;
	display: inline-block;
	font-size: 10px;
	color: rgb(147,153,159);
	line-height: 10px;
}
.seller-content .overview .desc .deliveryTime{
	border-right: none;
}
.seller-content .overview .desc .normal{
	margin-top: 4px;
	font-size: 24px;
	font-weight: 200;
	color: rgb(7,17,27);
	line-height: 24px;
}
.seller-content .overview .desc .normal span{
	font-size: 10px;
}
 .seller-content .notice{
 	padding: 16px 18px 0px 12px;
 }

 .seller-content .notice .title{
	margin-bottom: 8px;
	font-weight: 700;
	font-size: 14px;
	color: rgb(7,17,27);
	line-height: 14px;
 }
 .seller-content .notice .bulletin{
 	font-size: 12px;
 	font-weight: 200;
 	color: rgb(240,20,20);
 	line-height: 24px;
 	padding-bottom: 16px;
 	
 }
.seller-content .notice .supports .support{
	padding: 16px 0px;
	border-top: 1px solid rgba(7,17,27,0.1);
}
.seller-content .notice .supports .support .description{
	margin-left: 6px;
	vertical-align: top;
	font-size: 12px;
	font-size: 200;
	color: (7,17,27);
	line-height: 16px;
}
.seller-content .live-action {
	padding: 18px 0 18px 18px;
}
 .seller-content .live-action .pics-wrapper{
  width: 100%;
  overflow: hidden;
  white-space: nowrap;
 }
.seller-content .live-action .title{
	margin-bottom: 12px;
	font-weight: 700;
	font-size: 14px;
	color: rgb(7,17,27);
	line-height: 14px;
}
.seller-content .live-action .pics .pic{
	display: inline-block;
	margin-right: 6px;
  width: 120px;
  height: 90px;
	
}
 .seller-content .seller-infos{
 	padding: 18px 12px 0 18px;
 }
 .seller-content .live-action .pics .pic:last-child{
  margin-right: 0px;
 }
 .seller-content .seller-infos .title{
 	margin-bottom: 12px;
	font-weight: 700;
	font-size: 14px;
	color: rgb(7,17,27);
	line-height: 14px;
 }
  .seller-content .seller-infos .infos{
  	padding: 0px 0 16px 0px;
  }
   .seller-content .seller-infos .infos .info{
	padding: 16px 0 16px 12px;
	border-top: 1px solid rgba(7,17,27,0.1);
	font-size: 12px;
	font-weight: 200;
	color: rgb(7,17,27);
	line-height: 16px;
   }
/*活动的图标*/
.seller-content .notice .supports .icon{
	display: inline-block;
	vertical-align: top;
	width: 16px;
	height: 16px;
}
 .seller-content .notice .supports .icon.decrease{
 	background-image: url('./decrease_3@3x.png');
 	background-size: 12px 12px;
 	margin-right: 2px;
 	background-repeat: no-repeat;
 	background-position: center;
 }
 .seller-content .notice .supports .icon.discount{
 	background-image: url('./discount_3@3x.png');
 	background-size: 12px 12px;
 	margin-right: 2px;
 	background-repeat: no-repeat;
 	background-position: center;
 }
.seller-content .notice .supports .icon.guarantee{
 	background-image: url('./guarantee_3@3x.png');
 	background-size: 12px 12px;
 	margin-right: 2px;
 	background-repeat: no-repeat;
 	background-position: center;
 }
.seller-content .notice .supports .icon.invoice{
 	background-image: url('./invoice_3@3x.png');
 	background-size: 12px 12px;
 	background-repeat: no-repeat;
 	margin-right: 2px;
 	background-position: center;
 }
.seller-content .notice .supports .icon.special{
 	background-image: url('./special_3@3x.png');
 	background-size: 12px 12px;
 	margin-right: 2px;
 	background-repeat: no-repeat;
 	background-position: center;
 }

</style>



