<template>
  <div class="rating" ref="rating">
   <div class="rating-content">
   		<div class="overview">
	   		<div class="overview-left">
	   			<div class="score">{{seller.score}}</div>
	   			<h1 class="title">综合评分</h1>
	   			<div class="rank">高于周边商家{{seller.rankRate}}%</div>
	   		</div>
	   		<div class="overview-right">
	   			<div class="serviceScore"><span>服务态度</span><span class="start-warpper">
	   				<start :size="36" :score="seller.serviceScore"></start>
	   			</span><span class="server-num">{{seller.serviceScore}}</span></div>
	   			<div class="foodScore"><span>商品评价</span><span class="start-warpper">
	   				<start :size="36" :score="seller.foodScore"></start>
	   			</span><span class="server-num">{{seller.foodScore}}</span></div>
	   			<div class="deliveryTime"><span>送达时间</span><span class="time">{{seller.deliveryTime}}分钟</span></div>
	   		</div>
   		</div>
   		<split></split>	
   		
   		<div class="ratingselect-warpper ">
   			<ratingselect class="ratingselect" :ratings='ratings' :selectType="selectType" :desc="desc" :onlycontent="onlycontent"  @toggle="toggled($event)"  @select="selected($event)"></ratingselect>
   		</div>

   		<ul class="ratings">
   			<li class="ratings-item" v-for="rating in ratings" v-show="needShow(rating.rateType,rating.text)">
   				<div class="ratings-left">
   					<img :src="rating.avatar" alt="" class="avatar">
   					
   				</div>
   				<div class="ratings-right">
   						<div class="name">{{rating.username}}</div>
   						<span class="rateTime">{{rating.rateTime | formatDate}}</span>
   						<div class="start-warpper">
   						<start :size="24" :score="rating.score"></start>
   						<span class="deliveryTime" v-show="rating.deliveryTime">{{rating.deliveryTime}}分钟送达</span>
   						<div class="text">{{rating.text}}</div>
   						<div class="icon">
   							<i :class="{'icon-thumb_up':rating.rateType==0}"></i>
   							<i :class="{'icon-thumb_down':rating.rateType==1}"></i>
   								<span class="recommend-item" v-for="item in rating.recommend">{{item}}</span>
   						</div>
   					</div>
   				</div>
   			</li>
   		</ul>
   </div>
 
  </div>
</template>

<script>
import start from '../start/start'
import split from '../split/split'
import ratingselect from '../ratingselect/ratingselect'
import BScroll from 'better-scroll'
import {formatDate} from '../../common/js/date.js'


const POSITIVE=0
const NEGATIVE=1
const ALL=2
export default {
  components:{
  	start,
  	split,
  	ratingselect,
 },
  props:{
  	seller:{
  		type:Object,
  	}
  },
  data(){
  	return{
  		selectType:ALL,
  		desc:{
  			positive:"满意",
  			negative:"不满意",
  			all:"全部"
  		},
  		onlycontent:false,
  		ratings:[]

  		
  	}
  },
   filters:{
  	formatDate(time){
  		let date=new Date(time)
  		return formatDate(date,'yyyy-MM-dd hh:mm')
  	}
  },
  methods:{
  		selected(value){
			this.selectType=value
		},
		toggled(value){
			this.onlycontent=value
		
		},
		needShow(type,text){
			if(this.onlycontent && !text){
				return false
			}
			if(this.selectType==ALL){
				return true
			}else{
				return type===this.selectType
			}
			
		}
	},
  mounted(){

  },
created(){
	this.selectType=ALL
	this.desc={
		positive:"满意",
  			negative:"不满意",
  			all:"全部"
	}
	this.onlycontent=false
	this.$http.get("https://wd7517823586mbplep.wilddogio.com/ratings.json")
	.then((res)=>{
		this.ratings=res.body
	})
	this.$nextTick(()=>{
		if(!this.ratingsScroll){
			this.ratingsScroll=new BScroll(this.$refs.rating,{
			click:true
			})
		}else{
			this.ratingsScroll.refresh()
		}
	
	})
	
}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.rating{
		width: 100%;
		position: absolute;
		top:174px;
		left: 0px;
		bottom: 0px;
		overflow: hidden;
		
	}
	.rating .rating-content{
		
	}
	.rating .rating-content .overview{
		padding: 18px 24px 18px 0px;
		display:flex;
	}
	.rating .rating-content .overview .overview-left{
		flex: 0 0 137px;


	}
	@media only screen and (max-width: 320px) {
		.rating .rating-content .overview .overview-left{
		flex: 0 0 100px;


	}
	}
	.rating .rating-content .overview .overview-left .score{
		text-align: center;
		margin-bottom: 6px;
		font-size: 24px;
		color: rgb(255,153,0);
		line-height: 28px;

	}
	.rating .rating-content .overview .overview-left .title{
		text-align: center;
		margin-bottom: 8px;
		font-size: 12px;
		color: rgb(17,17,27);
		line-height: 12px;
	}
	.rating .rating-content .overview .overview-left .rank{
		text-align: center;
		font-size: 10px;
		color: rgb(147,157,159);
		line-height: 10px;
	}
	
	.rating .rating-content .overview .overview-right{
		flex: 1;
		padding-left: 24px;
		border-left: 1px solid rgba(7,17,27,0.1);
		font-size: 0px;
		color: rgb(7,17,27);
		line-height: 18px;
	}
	.rating .rating-content .overview .overview-right .serviceScore,
	.rating .rating-content .overview .overview-right .foodScore{
		margin-bottom: 8px;
		font-size: 12px;
	}
	.rating .rating-content .overview .overview-right .start-warpper{
		padding: 0 12px;
		font-size: 12px;
	}
	.rating .rating-content .overview .overview-right .server-num{
		font-size: 12px;
		color: rgb(255,153,0);
		line-height: 18px;
	}
	.rating .rating-content .overview .overview-right .deliveryTime{
		font-size: 12px;
	}
	.rating .rating-content .overview .overview-right .deliveryTime .time{
		margin-left: 12px;
		font-size: 12px;
		color: rgb(147,153,159);
		line-height: 18px;
	}

	.rating .rating-content .ratings{
		padding: 18px 0 0 18px;
	}
	.rating .rating-content .ratings .ratings-item{
		display: flex;
		padding: 18px 0px;
		border-bottom: 1px solid rgba(7,17,27,0.1);
	}
	.rating .rating-content .ratings .ratings-item .ratings-left{
		flex: 0 0 40px;
		padding-right: 12px;
	}
	 .rating .rating-content .ratings .ratings-item .ratings-left .avatar{
	 		border-radius: 50%;
	 		width: 28px;
	 		height: 28px;
	 }
	.rating .rating-content .ratings .ratings-item .ratings-left 
	.rating .rating-content .ratings .ratings-item .ratings-right{
		flex: 1;
	}
	.rating .rating-content .ratings .ratings-item .ratings-right .name{
		margin-bottom: 4px;
		font-size: 10px;
		color: rgb(7,17,27);
		line-height: 12px;
	}
	.rating .rating-content .ratings .ratings-item .ratings-right{
		position: relative;
		width: 100%;
	}
	.rating .rating-content .ratings .ratings-item .ratings-right .rateTime{
		position: absolute;
		top:0;
		right:18px;
		font-size: 10px;
		font-weight: 200;
		color: rgb(147,153,159);
		line-height: 12px;
	}
	.rating .rating-content .ratings .ratings-item .ratings-right .text{
		margin-top: 8px;
		font-size: 12px;
		color: (7,17,27);
		line-height: 18px;
	}
	.rating .rating-content .ratings .ratings-item .ratings-right .deliveryTime{
		margin-left: 6px;
		font-size: 10px;
		font-weight: 200;
		color: rgb(147,153,159);
		line-height:12x;
	}
	.rating .rating-content .ratings .ratings-item .ratings-right .icon{
		margin-right: 8px;
		margin-top: 8px;
		font-size: 12px;
		line-height: 16px;
	}
	.rating .rating-content .ratings .ratings-item .ratings-right .icon .recommend-item{
		padding: 0px 6px;
		margin-right: 8px;
		border: 1px solid rgba(7,17,27,0.1);
		border-width: 1px;
		border-radius: 1px;
		color: rgb(147,153,159);
		line-height: 16px;
		font-size: 9px;
	}
	.rating .rating-content .ratings .ratings-item .ratings-right .icon .icon-thumb_up{
		color: rgb(0,160,220);
	}
	.rating .rating-content .ratings .ratings-item .ratings-right .icon .icon-thumb_down{
		color: rgb(183,187,191);
	}
</style>



