<template>
<transition name="move">
  <div class="food" v-show="showFlag" ref="foodContent">
   	<div class="food-content" >
   		<div class="image-header">
   			<img :src="food.image" alt="">
   			<div class="back" @click="back">
   				<i class="icon-arrow_lift"></i>
   			</div>
   		</div>
   		<div class="content">
   			<h1 class="title">{{food.name}}</h1>
   			<div class="detail">
   				<span class="sell-count">月售{{food.sellCount}}份</span>
   				<span class="rating">好评率{{food.rating}}%</span>
   			</div>
   			<div class="price">
			<span class="newPrice">￥{{food.price}}</span><span class="oldPrice" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
			<div class="cartcontrol-wrapper" v-show="food.count">
	   			<cartcontrol :food="food" @ballsnum="clickBall"></cartcontrol>	
	   			</div>
	   		<transition name="buyfade">	
	   		<div class="buy" v-show="!food.count || food.count==0" @click.stop.prevent="addFirst">加入购物车</div>
	   		</transition>
			</div>
			
 		</div>
   			<split v-show="food.info"></split>
   			<div class="info" v-show="food.info">
   				<h1 class="title">商品介绍</h1>
   				<div class="text">
   					{{food.info}}
   				</div>
   			</div>
   			<split></split>
				<div class="ratings">
					<h1 class="title">商品评论</h1>
					<ratingselect :ratings="food.ratings" :desc="desc" :onlycontent="onlycontent" :select-type="selectType" @toggle="toggled($event)"  @select="selected($event)"></ratingselect>
					<div class="ratings-wrapper" >
						<ul v-show="food.ratings">
							<li v-for="rating in food.ratings" class="rating-item" v-show="needShow(rating.rateType,rating.text)">
								<div class="user">
									<div class="name">
										{{rating.username}}
									</div>
									<img class="avatar" :src="rating.avatar" width="12" height="12" alt="">
									</div>
									<div class="time" >
										{{rating.rateTime | formatDate}}
									</div>
										
									<p class="text-wrapper">
										<span :class="{'icon-thumb_up': rating.rateType===0,'icon-thumb_down':rating.rateType===1}"></span>
											<span class="text">{{rating.text}}</span>
									</p>
								
							</li>
						</ul>
						<div class="no-ratings" v-show="!food.ratings">
							暂无评价
						</div>
					</div>
				</div>
			</div>
   		</div>
	   		
   	</div>
</transition>
</template>

<script>
import cartcontrol from '../cartcontrol/cartcontrol'
import split from '../split/split'
import ratingselect from '../ratingselect/ratingselect'
import BScroll from 'better-scroll'
import Vue from 'vue'
import {formatDate} from '../../common/js/date.js'


const POSITIVE=0
const NEGATIVE=1
const ALL=2
export default {
	components:{
		cartcontrol,
		split,
		ratingselect,
		ratings:[]
	},
  data(){
  	return{
  		showFlag:false,
  		selectType:ALL,
  		desc:{
  			positive:"推荐",
  			negative:"吐槽",
  			all:"全部"
  		},
  		onlycontent:true,
  	}
  },
  props:{
  	food:{
  		type:Object
  	}
  },
  filters:{
  	formatDate(time){
  		let date=new Date(time)
  		return formatDate(date,'yyyy-MM-dd hh:mm')
  	}
  },

  methods:{
		show(){
			this.showFlag=true
			this.ratings=this.food.ratings
			this.desc={
				positive:"推荐",
  				negative:"吐槽",
  				all:"全部"
			}
			this.onlycontent=true
			this.selectType=ALL
			this.$nextTick(()=>{
				if(!this.foodScroll){
					this.foodScroll=new BScroll(this.$refs.foodContent,{
					click:true
					})
				}else{
					this.foodScroll.refresh()
				}
				
			})
		},
		back(){
			this.showFlag=false
		},
		addFirst(){
			Vue.set(this.food,"count",1)
		},
		clickBall(value){
			this.$emit("ballsnum",value)
		},
		selected(value){
			this.selectType=value
		},
		toggled(value){
			this.onlycontent=value
		
		},
		needShow(type,text){
			if(this.onlycontent && !text){
				return false
			}
			if(this.selectType==ALL){
				return true
			}else{
				return type===this.selectType
			}
			
		}
	}
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.food{
		position: fixed;
		top: 0;
		bottom: 48px;
		width: 100%;
		z-index: 30;
		background: #fff;
	}
	.move-enter-active,.move-leave-active{
		transform:translate3d(0,0,0);
		transition: all 0.3s;
	}
	.move-enter,.move-leave-to{
		transform:translate3d(100%,0,0);
	}
	.image-header{
		position: relative;
		width: 100%;
		height: 0;
		padding-top: 100%;
	}
	.food .food-content .image-header img{
		position: absolute;
		top:0;
		left: 0;
		height: 100%;
		height: 100%;

	}
	.food .food-content .image-header .back{
		position: absolute;
		top: 0;
		left: 0;
		

	}
	.food .food-content .image-header .back .icon-arrow_lift{
		display: block;
		padding: 10px;
		font-size: 20px;
		color: #fff;
	}
	/*内容*/
	.food .food-content .content{
		margin-bottom: 18px;
		margin-left: 18px;
		font-size: 0px;
	}
	.food .food-content .content .title{
		margin-top:18px;
		font-size: 14px;
		font-weight: 700;
		color: rgb(7,17,27);
		line-height: 14px;
	}
	 .food .food-content .content .detail{
		margin-top: 8px;
		color: rgb(147,153,159);
		font-size: 10px;
		line-height: 10px;
	 }
	 .food .food-content .content .detail span{
	 	margin-right: 12px;
	 }
	 .food .food-content .content .price {
	 	position: relative;
	 	margin-top: 18px;
	 	font-weight: 700;
	 	line-height: 24px;
	 }
	 .food .food-content .content .price .newPrice{
	 	margin-right: 12px;
		font-size: 14px;
		text-decoration: line-through;
		color: rgb(240,20,20);
	 }
	 .food .food-content .content .price .oldPrice{
	 	font-size: 10px;
	 	color: rgb(147,153,159);
	 }
	 .food .food-content .content .price  .cartcontrol-wrapper{
	 	position: absolute;
	 	right: 12px;
	 	bottom: 12px;
	 	
	 }
	.food .food-content .content .price .buy{
	 	position: absolute;
	 	border-radius: 12px;
	 	text-align: center;
	 	line-height: 24px;
	 	right:18px;
	 	bottom:18px;
	 	font-size: 10px;
	 	color: rgb(255,255,255);
	 	width: 74px;
	 	height: 24px;
	 	background: rgb(0,160,220);
	 }
	 .buyfade-enter-active,.buyfade-leave-active{
	 	opacity: 1;
	 	transition:all 0.5s;
	 }
	 .buyfade-enter,.buyfade-leave-to{
	 	opacity: 0;
	 }
	.food .food-content .info{
		padding: 18px;
	}
	.food .food-content .info .title{
		font-size: 14px;
		font-weight: 700;
		color:rgb(7,17,27);
		line-height: 14px;
		margin-bottom: 6px;
	}
	.food .food-content .info .text{
		padding: 0 8px;
		font-size: 12px;
		font-weight: 200;
		color: rgb(77,85,93);
		line-height: 24px;
	}
	.food .food-content .ratings .title{
		padding: 18px 18px 0 18px;

	}
	.food .food-content .ratings .ratings-wrapper{
		padding: 0px 18px;
		
	}
	.food .food-content .ratings .ratings-wrapper .rating-item{
		position: relative;
		padding: 16px 0;
		border-bottom:1px solid rgba(7,17,27,0.1);
	}
	.food .food-content .ratings .ratings-wrapper .rating-item .user{
		position: absolute;
		top:16px;
		right: 12px;
		line-height: 12px;

	}
	.food .food-content .ratings .ratings-wrapper .rating-item .user .name,.food .food-content .ratings .ratings-wrapper .rating-item .time{
		display: inline-block;
		font-size: 10px;
		color: rgb(147,153,159);
		line-height: 12px;
	}
	.food .food-content .ratings .ratings-wrapper .rating-item .user .avatar{
		border-radius: 50%;


	}
	.food .food-content .ratings .ratings-wrapper .rating-item .text-wrapper{
		margin-top: 6px;
		font-size: 12px;
	}
	.food .food-content .ratings .ratings-wrapper .rating-item .text-wrapper .text{
		color: rgb(7,17,27);
		line-height: 16px;
	}
	.food .food-content .ratings .ratings-wrapper .rating-item .text-wrapper .icon-thumb_up{
		line-height: 16px;
		color: rgb(0,160,220);
	}
	.food .food-content .ratings .ratings-wrapper .rating-item .text-wrapper .icon-thumb_down{
		line-height: 16px;
		color: rgb(147,153,159);
	}
	.food .food-content .ratings .ratings-wrapper .no-ratings{
		padding:0 16px;
		font-size: 16px;
		color: rgb(147,153,159);
	}
</style>



